import "./Navbar.css";
import Carousel from "../images/carousel_1.png";
import Carousel_2 from "../images/carousel_2.png";
import Logo from "../images/Client_Logo.png";
import {Link} from 'react-scroll';

function Navbar() {
  
  return (
    <div>
      <nav className="my-nav navbar navbar-expand-lg">
        <div className="container-fluid">
          <img src={Logo} className="navbar-brand logo" />
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link to="carouselExampleIndicators" className="nav-link" spy={true} smooth={true} offset={50} duration={500} >Home</Link>
              </li>
              <li className="nav-item">
              <Link to="Services" className="nav-link" spy={true} smooth={true} offset={-20} duration={500} >Our Services</Link>
              </li>
              <li className="nav-item">
              <Link to="About" className="nav-link" spy={true} smooth={true} offset={-20} duration={500} >About Us</Link>
              </li>
              <li className="nav-item">
              <Link to="contactUs" className="nav-link" spy={true} smooth={true} offset={-100} duration={500} >Contact Us</Link>

              </li>
            </ul>
            <div className="ms-auto">
              <button className="btn btn-primary LoginBtn">Log-in</button>
              <button className="btn btn-light SignUpBtn">Sign-Up</button>
            </div>
          </div>
        </div>
      </nav>

      <div id="carouselExampleIndicators" class="carousel slide">
        <div class="carousel-indicators">
          <button
            type="button"
            data-bs-target="#carouselExampleIndicators"
            data-bs-slide-to="0"
            class="active"
            aria-current="true"
            aria-label="Slide 1"
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleIndicators"
            data-bs-slide-to="1"
            aria-label="Slide 2"
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleIndicators"
            data-bs-slide-to="2"
            aria-label="Slide 3"
          ></button>
        </div>
        <div class="carousel-inner">
          <div class="carousel-item active">
            <img src={Carousel} class="carousel_img d-block" alt="..." />
          </div>
          <div class="carousel-item">
            <img src={Carousel_2} class="carousel_img d-block" alt="..." />
          </div>
          <div class="carousel-item">
            <img src={Carousel} class="carousel_img d-block" alt="..." />
          </div>
        </div>
        <button
          class="carousel-control-prev"
          type="button"
          data-bs-target="#carouselExampleIndicators"
          data-bs-slide="prev"
        >
          <span
            class="carousel-control-prev-icon previous"
            aria-hidden="true"
          ></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button
          class="carousel-control-next"
          type="button"
          data-bs-target="#carouselExampleIndicators"
          data-bs-slide="next"
        >
          <span
            class="carousel-control-next-icon next"
            aria-hidden="true"
          ></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>
    </div>
  );
}

export default Navbar;
