import {useState, useEffect} from 'react'
import './Contact.css';

export default function Contact() {

    const [formData, setFormData] = useState({
        name: "",
        email: "",
        message:"",
    });

    useEffect(() => {
      }, [formData]);
    
      const handleChange = (event) => {
        const { name, value } = event.target;
        setFormData(prevFormData => ({
          ...prevFormData,
          [name]: value,
        }));
      };
  return (
    <div className='container-fluid steps'>
        <div className='container mt-5' id='contactUs'>
            <h1 className="contactHeader text-center">Contact Us</h1>
            <p className='contactParagraph text-center'>"Your feedback is the compass that guides us. Be a part of our journey."</p>
        </div>
        <div className='container'>
            <div className='row justify-content-between'>
                <div className='col-lg-5 col-sm-12 contact'>
                    <div className='row contactNumber'>
                        <i className="col-sm-6 col-lg-4 fa-solid fa-phone-volume "></i>
                        <span className='col-sm-6 col-lg-8 text-start'>Call Us</span>
                        <p className='col text-center'>+63 (900)-0000-000</p>
                    </div>
                    <div className='row emailAddress'>
                        <i className="col-sm-6 col-lg-4 fa-solid fa-envelope "></i>
                        <span className='col-sm-6 col-lg-8 text-start'>Email Us</span>
                        <p className='col text-center'>sampleEmail123@sample.com</p>
                    </div>
                    <div className='row location'>
                    <i class="col-sm-6 col-lg-4 fa-solid fa-location-dot "></i>
                        <span className='col-sm-6 col-lg-8 text-start'>Location</span>
                        <p className='col text-center'>123 Sample Address of the company, PH 1000</p>
                    </div> 
                    <div className='row businessHours'>
                    <i class="col-sm-6 col-lg-4 fa-solid fa-clock "></i>
                        <span className='col-sm-6 col-lg-8 text-start'>Business Hours (Philippine Time)</span>
                        <p className='col text-center'>Mon-Fri: 8:00 am to 5:00 pm<br/>Sat-Sun: 8:00 am to 3:00 pm</p>
                    </div> 
                </div>
                <div className='col-lg-6 col-sm-12 bookAppointment'>
                    <h5 className='text-center appointment'>Book an Appointment</h5>
                    <div className='appointmentFormWrapper'>
                        <form className='row justify-content-space justify-content-center'>
                            <label htmlFor='fullname' className='col-3'>Name:</label>
                            <input className='col-8 mb-4' type='text' id='fullname' name='name' placeholder='Input your Fullname' value={formData.name} onChange={handleChange} required/>
                        
                            <label className='col-3 mb-4' htmlFor='email'>Email Address:</label>
                            <input className='col-8 mb-4' type='email' id='email' name='email' placeholder='Input a valid email address' value={formData.email} onChange={handleChange} required/>

                            <label className='col-11' htmlFor='message'>Message:</label>
                            <textarea className='col-11 ms-3 mb-4' rows={4} id='message' name='message' value={formData.message} onChange={handleChange} required/>
                        
                            <button type='submit' className='btn btn-danger submitBtn mb-3'>Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
